import { Router } from '@angular/router';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable,Subject } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class StudentService {

  constructor(private http:HttpClient,private router:Router) { }

  public studId:any;
  public currentUser: Subject<string> = new Subject<string>();
  signup(student: any): Observable<any> {
    return this.http.post('http://localhost:3000/student/signup',
      {'username':student.email,'password':student.password})
      .pipe(map((res: any) => {
          
          console.log(res);
          alert(res.status);
          //return {'item':res};
      }),
       );
  }
  login(student: any): Observable<any> {
    return this.http.post('http://localhost:3000/student/login',
      {'username':student.email,'password':student.password})
      .pipe(map((res: any) => {
          
          console.log(res);
          this.currentUser.next(res.user);
          localStorage.setItem('token',res.token);
          
          this.router.navigate(['/quiz',res.id]);
          this.studId=res.id;        
      }),
       );
  }
  logout(){
    localStorage.removeItem('token');
    this.router.navigateByUrl('/home')
  }
  addScore(score: any): Observable<any> {
    return this.http.put('http://localhost:3000/student/'+this.studId,
      {'marks':score})
      .pipe(map((res: any) => {
          
          console.log(res.marks);
          return res.marks;
      }),
       );
  }
  getScore(): Observable<any> {
    return this.http.put('http://localhost:3000/student/'+this.studId,
      {})
      .pipe(map((res: any) => {
          
          console.log(res);
          alert(res.marks);
          //return {'item':res};
      }),
       );
  }

}
