import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable,Subject } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class TeacherService {
  

  constructor(private http:HttpClient,private router:Router) { }
   public currentUser: Subject<string> = new Subject<string>();

  signup(teacher: any): Observable<any> {
    return this.http.post('http://localhost:3000/teacher/signup',
      {'username':teacher.email,'password':teacher.password})
      .pipe(map((res: any) => {
          
          if(res.success == true){
            alert(res.status);
            return {status:" "}
          }
          else{
            return {status:"user with given data already exist!"}
          }
          
          
      }),
       );
  }
  login(teacher: any): Observable<any> {
    
    return this.http.post('http://localhost:3000/teacher/login',
      {'username':teacher.email,'password':teacher.password})
      .pipe(map((res: any) => {
          
          console.log(res.id);

          if(res.success==true){
            this.currentUser.next(teacher.email);
            localStorage.setItem('token',res.token);
            
            this.router.navigate(['/question',teacher.email]);  
            return {status:""}
          }
          else{
            return {status:"username or password is incorrect.."}
          }
           
      }),
       );
  }
  
  logout(){
    localStorage.removeItem('token');
    this.router.navigateByUrl('/home')
  }
}
