import { Component, OnInit,OnDestroy } from '@angular/core';
import { StudentService } from '../services/student.service';
import { TeacherService } from '../services/teacher.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit,OnDestroy {

  constructor(private studentService:StudentService,private teacherService:TeacherService,
              private route:Router) { }

  user=undefined;
  
  subscription: Subscription;

  ngOnInit(): void {

    this.subscription = this.studentService.currentUser
    .subscribe(currentUser => {  this.user = currentUser; });
    
    this.subscription = this.teacherService.currentUser
    .subscribe(currentUser => {  this.user = currentUser; });

    if(this.user==undefined){
      this.studentService.logout();
    }
  }
ngOnDestroy(){
this.subscription.unsubscribe();
}

  logout(){
    this.user=undefined;
    this.studentService.logout();
  }

  
}
