import { Component, OnInit } from '@angular/core';
import { AddQuestionService } from '../services/add-question.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-questions',
  templateUrl: './add-questions.component.html',
  styleUrls: ['./add-questions.component.css']
})
export class AddQuestionsComponent implements OnInit {

  constructor(private questionService:AddQuestionService,private route:ActivatedRoute) { }

  ngOnInit(): void {
    this.email=this.route.snapshot.paramMap.get('id');
  }
   public question={question_text:'',option1:'',option2:'',option3:'',option4:'',correct_option:''};
   public email:any;
   public teacherReady =false;
  
   addQuestion(){
     if(this.question.question_text=="" || this.question.option1==""|| this.question.option2==""|| this.question.option2==""
     || this.question.option3=="" || this.question.option4=="" || this.question.correct_option==""){
      alert("please fill all the fields");
     }
     else{
      this.questionService.add(this.question)
      .subscribe((res: any) => {
        this.question.question_text="";
        this.question.option1="";
        this.question.option2="";
        this.question.option3="";
        this.question.option4="";
        this.question.correct_option="";
      },
        (error: any) => {
        console.log(error);
      });
     }
    
  }

  addQuiz(){
    this.teacherReady=true;
  }

  
}
