import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AddQuestionService {

  constructor(private http:HttpClient) { }

  add(question: any): Observable<any> {
    return this.http.post('http://localhost:3000/questions/add',
      {'question':question.question_text,'option1':question.option1,'option2':question.option2,
      'option3':question.option3,'option4':question.option4,'correct_answer':question.correct_option,})
      .pipe(map((res: any) => {
          
          console.log(res);
          alert("question added..");
          //return {'item':res};
      }),
       );
  }
  getQuestions():Observable<any>{  
    
    return this.http.get('http://localhost:3000/questions/get')
          .pipe(map(res =>{
            return {'questions':res}
           //console.log(res);
          })
          )
  }
  }
  

