import { Component, OnInit } from '@angular/core';
import { StudentService } from '../services/student.service';
@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit {

  constructor(private studentService:StudentService) { }
  public student={email:'',password:''};
  public invalidForm:any;
  public errMsg="";
  
  ngOnInit(): void {
  }

  studentSignup(){
    if(this.student.email=="" ||this.student.password==""  ){
      this.invalidForm=true;
    }
    else{
      this.invalidForm=false;
      this.studentService.signup(this.student)
    .subscribe((res: any) => {
    },
      (error: any) => {
      console.log(error);
      this.errMsg="user with given data already exist!";
    });
    }
    
   }

   studentLogin(){
     if(this.student.email=="" ||this.student.password==""  ){
       this.invalidForm=true;
     }
     else{
       this.invalidForm=false;
      this.studentService.login(this.student)
      .subscribe((res: any) => {
  
        
      },
        (error: any) => {
        console.log(error);
        this.errMsg="username or password is incorrect..";
      });
     }
    
   }
}
