import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddQuestionsComponent } from './add-questions/add-questions.component';
import { HeaderComponent } from './header/header.component';
import { StudentComponent } from './student/student.component';
import { TeacherComponent } from './teacher/teacher.component';
import {QuizComponent} from './quiz/quiz.component';
import {HomeComponent} from './home/home.component'
import { from } from 'rxjs';
const routes: Routes = [
  {path:'',component:HomeComponent},
  {path:'home',component:HomeComponent},
  {path:'student',component:StudentComponent},
  {path:'teacher',component:TeacherComponent},
  {path:'question',component:AddQuestionsComponent},
  {path:'question/:id',component:AddQuestionsComponent},
  {path:'quiz',component:QuizComponent},
  {path:'quiz/:id',component:QuizComponent},
  {path:'header',component:HeaderComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents=[HeaderComponent,StudentComponent,TeacherComponent,AddQuestionsComponent,QuizComponent,HomeComponent];
